.login-container {
    height: 100vh;
    background-color: #fafafa;
    display: block;
}
.login-header {
    height: 64px;
    /*box-shadow: 2px 0 6px rgba(0,0,0,.03);*/
    box-shadow: 2px 0 8px rgba(0,21,41,.1);
}
.login-header .logo {
    padding-top: 25px;
    padding-left: 30px;
    font-size: 18px;
    font-weight: bold;
}

.login-container-wrapper {
    position: relative;
    top: 10%;
    padding: 30px;
    margin: 0 auto;
    width: 420px;
    background-color: #ffffff;
    box-shadow: 0 2px 8px rgba(0,0,0,.03);
}
.login-container-wrapper h1 {
    font-size: 24px;
    font-weight: 500;
    color: #333333;
}
.login-container-wrapper form {
    margin-top: 30px;
}
.login-form {
    max-width: 300px;
}
.login-form-button {
    width: 100%;
}
.login-forgot-password {
    text-align: right;
}